body {
  color: rgb(55, 55, 55);
}

h1 {
  color: #c99599;
  text-align: center;
  font-size: 80px;
}

.toDoContainer {
  margin: 0 auto;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #fff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.newInput {
  color: rgb(55, 55, 55);
  font-size: 20px;
  padding: 16px 16px 16px 55px;
  border: 0.5px solid #d8d4d4;
}

